* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.button {
  color: #000;
  cursor: pointer;
  background-color: #fff;
  border: 2px solid #daa520;
  border-radius: 4px;
  gap: 5px;
  padding: 5px 10px;
  font-size: 20px;
  transition: color .25s linear, border-color .25s linear, background-color .25s linear, text-shadow .25s linear;
  display: inline-flex;
}

.button:disabled {
  color: #a9a9a9;
  border-color: #a9a9a9;
}

.button:hover {
  color: #b22222;
  border-color: #b22222;
}

.input {
  text-align: center;
  border-color: #daa520;
  border-radius: 4px;
  padding: 5px 10px;
  font-size: 20px;
}

.timer {
  color: #000;
  gap: 5px;
  font-size: 18px;
  display: flex;
}

